<template>
  <div>
    <div style="margin-bottom: 15px">
      <el-button type="primary" style="margin-left: 10px" @click="goToAppointment()"></el-button>
    </div>
    <div>
      <!--      {-->
      <!--      "id": 5,-->
      <!--      "userId": 1,-->
      <!--      "userName": "2",-->
      <!--      "technicianId": 16,-->
      <!--      "technicianName": "-2",-->
      <!--      "serviceId": 1,-->
      <!--      "serviceName": "SPA",-->
      <!--      "salonId": 1,-->
      <!--      "salonName": null,-->
      <!--      "appointmentTime": "2024-09-10 19:30:00",-->
      <!--      "time": 30,-->
      <!--      "finishTime": "2024-09-10 20:00:00",-->
      <!--      "status": "PENDING",-->
      <!--      "createdAt": "2024-09-10 15:11:32",-->
      <!--      "updatedAt": "2024-09-10 15:11:32"-->
      <!--      }-->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="salonName" label="" align="center"></el-table-column>
        <el-table-column prop="serviceName" label="" align="center"></el-table-column>
        <el-table-column prop="time" label="" align="center"></el-table-column>
        <el-table-column prop="appointmentTime" label="" align="center"></el-table-column>
        <el-table-column prop="statusName" label="" align="center"></el-table-column>
        <el-table-column prop="createdAt" label="" align="center"></el-table-column>
        <el-table-column v-if="user.role === 'ADMIN'" label="" align="center">
          <template slot-scope="scope">
            <!--            <el-button type="primary" @click="edit(scope.row)"></el-button>-->
            <el-popconfirm title="" @confirm="cancelAppointment(scope.row)">
              <el-button slot="reference" type="danger" style="margin-left: 5px"></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="margin-top: 10px">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[20,50,100]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="total">
      </el-pagination>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "MyAppointment",
  data() {
    return {
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      params: {
        name: '',
        pageNum: 1,
        pageSize: 20
      },
      tableData: [],
      total: 0,
      dialogFormVisible: false,
      form: {},
      teacherData: [],
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
        ],
        teacherId: [
          {required: true, message: '', trigger: 'blur'},
        ]
      }
    }
  },
  // created
  created() {
    this.findBySearch();
    // this.loadTeacher();
  },
  // 
  methods: {
    findBySearch() {
      request.get("/salon/findByUserId?userId=" + this.user.id, {
        params: null
      }).then(res => {
        if (res.code === 0) {
          this.tableData = res.data;
          this.total = res.data.length;
        } else {
          console.log(res);
          // this.$message.error(res.msg);
          // this.$router.push("/login");
        }
      }).catch(err => {
        console.log(err);
      });
    },
    reload() {
      this.dialogFormVisible = false
      this.findBySearch();
    },
    handleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.findBySearch();
    },
    handleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.findBySearch();
    },

    cancelAppointment(appointment) {
      let data = {
        id: appointment.id,
        status: 'CANCELED'
      };

      request.post("/salon/setAppointmentStatus", JSON.stringify(data)).then(res => {
        if (res.code === 0) {
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          });
          this.findBySearch();
        } else {
          this.$notify.error({
            title: '',
            message: '' + res.msg,
          });
        }
      }).catch(err => {
        console.log(err);
      });
    },
    goToAppointment() {
      this.$router.push("/appointment");
    }
  }
}
</script>
