<template>
  <div>
    <div style="margin-bottom: 15px">
      <el-input v-model="params.name" style="width: 200px" placeholder=""></el-input>
      <!--      <el-input v-model="params.phone" style="width: 200px; margin-left: 5px" placeholder=""></el-input>-->
      <el-button type="warning" style="margin-left: 10px" @click="loadSalon()"></el-button>
      <!--      <el-button type="warning" style="margin-left: 10px" @click="reset()"></el-button>-->
      <el-button type="primary" style="margin-left: 10px" @click="add()"></el-button>
    </div>
    <div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="" width="50" align="center"></el-table-column>
        <el-table-column prop="name" label="" align="center"></el-table-column>
        <el-table-column prop="address" label="" align="center"></el-table-column>
        <el-table-column prop="phone" label="" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="edit(scope.row)"></el-button>
            <el-popconfirm title="" @confirm="del(scope.row.id)">
              <el-button slot="reference" type="danger" style="margin-left: 5px"></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="margin-top: 10px">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[20,50,100]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="total">
      </el-pagination>
    </div>
    <div>
      <el-dialog title="" :visible.sync="dialogFormVisible" width="30%">
        <el-form :model="form">
          <el-form-item label="" label-width="20%">
            <el-input v-model="form.name" autocomplete="off" style="width: 90%"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="20%">
            <el-input v-model="form.address" autocomplete="off" style="width: 90%"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="20%">
            <el-input v-model="form.phone" autocomplete="off" style="width: 90%"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="reload()"> </el-button>
          <el-button type="primary" @click="submit()"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "Salon",
  data() {
    return {
      params: {
        name: '',
        pageNum: 1,
        pageSize: 20
      },
      tableData: [],
      total: 0,
      dialogFormVisible: false,
      form: {},
      flag: false
    }
  },
  // created
  created() {
    this.loadSalon();
  },
  // 
  methods: {
    loadSalon() {
      request.get("/salon/findAll", {
        params: null
      }).then(res => {
        console.log(res);
        if (res.code === 0) {
          console.log("");
          let salonList = res.data;
          if (this.params.name) {
            salonList = salonList.filter(item => item.name.includes(this.params.name));
          }
          this.tableData = salonList;
          this.total = salonList.length;
        } else {
          // this.$message.error(res.msg);
          this.$notify({
            title: '',
            message: '' + res.msg + '',
            type: 'warning'
          });
          // this.$router.push("/login");
        }
      }).catch(err => {
        console.log(err);
      });
    },
    reload() {
      this.flag = false;
      this.dialogFormVisible = false
      this.loadSalon();
    },
    add() {
      this.form = {};
      this.dialogFormVisible = true;
    },
    edit(obj) {
      this.flag = true;
      this.form = obj;
      this.dialogFormVisible = true;
    },
    reset() {
      this.params = {
        pageNum: 1,
        pageSize: 20,
        name: '',
        phone: ''
      }
      this.loadSalon();
    },
    handleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.loadSalon();
    },
    handleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.loadSalon();
    },
    submit() {
      request.post("/salon/saveOrUpdateSalon", this.form).then(res => {
        if (res.code === 0) {
          this.$message.success('');
          this.dialogFormVisible = false;
          this.loadSalon();
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    },
    del(id) {
      request.delete("/salon/" + id).then(res => {
        if (res.code === 0) {
          this.$message.success('');
          this.loadSalon();
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    }
  }
}
</script>
