<script>
import request from "@/utils/request";

export default {
  name: "Service",
  data() {
    return {
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      params: {
        name: '',
        pageNum: 1,
        pageSize: 20
      },
      tableData: [],
      total: 0,
      dialogFormVisible: false,
      form: {},
      teacherData: [],
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
        ],
        teacherId: [
          {required: true, message: '', trigger: 'blur'},
        ]
      }
    }
  },
  // created
  created() {
    this.findBySearch();
    // this.loadTeacher();
  },
  // 
  methods: {
    findBySearch() {
      request.get("/service/findAll", {
        params: null
      }).then(res => {
        if (res.code === 0) {
          this.tableData = res.data;
          this.total = res.data.length;
        } else {
          console.log(res);
          // this.$message.error(res.msg);
          // this.$router.push("/login");
        }
      }).catch(err => {
        console.log(err);
      });
    },
    // loadTeacher() {
      // request.get("/user/findAll").then(res => {
      //   if (res.code === 0) {
      //     this.teacherData = res.data;
      //   } else {
      //     console.log(res);
      //     // this.$message.error(res.msg);
      //   }
      // }).catch(err => {
      //   console.log(err);
      // })
    // },
    reload() {
      this.dialogFormVisible = false
      this.findBySearch();
    },
    add() {
      this.form = {};
      this.dialogFormVisible = true;
    },
    edit(obj) {
      this.form = obj;
      this.dialogFormVisible = true;
    },
    reset() {
      this.params = {
        pageNum: 1,
        pageSize: 20,
        name: '',
        phone: ''
      }
      this.findBySearch();
    },
    handleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.findBySearch();
    },
    handleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.findBySearch();
    },
    submit() {
      request.post("/service/saveOrUpdate", this.form).then(res => {
        if (res.code === 0) {
          this.$message.success('');
          this.dialogFormVisible = false;
          this.findBySearch();
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    },
    successUploadPic(res) {
      this.form.pic = res.data;
    },
    successUploadPicIntro(res) {
      this.form.picIntroduce = res.data;
    },
    del(id) {
      request.delete("/type/" + id).then(res => {
        if (res.code === 0) {
          this.$message.success('');
          this.findBySearch();
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    }
  }
}
</script>
