<template>
  <div>
    <div style="margin-bottom: 15px">
      <el-select v-model="params.salonId" placeholder="" @change="findBySearch">
        <el-option
            v-for="item in salonList"
            :key="item.id"
            :label="item.name"
            :value="item.id">
        </el-option>
      </el-select>
      <!--      <el-input v-model="params.name" style="width: 200px" placeholder=""></el-input>-->
      <!--      <el-input v-model="params.phone" style="width: 200px; margin-left: 5px" placeholder=""></el-input>-->
      <!--      <el-button type="warning" style="margin-left: 10px" @click="findBySearch()"></el-button>-->
      <!--      <el-button type="warning" style="margin-left: 10px" @click="reset()"></el-button>-->
      <!--      <el-button type="primary" style="margin-left: 10px" @click="add()"></el-button>-->
    </div>
    <div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="salonName" label="" align="center"></el-table-column>
        <el-table-column prop="serviceName" label="" align="center"></el-table-column>
        <el-table-column prop="time" label="" align="center"></el-table-column>
        <el-table-column prop="appointmentTime" label="" align="center"></el-table-column>
        <el-table-column prop="statusName" label="" align="center"></el-table-column>
        <el-table-column prop="createdAt" label="" align="center"></el-table-column>
        <el-table-column label="" align="left" style="width:200px">
          <template slot-scope="scope">
            <!--            <el-button type="primary" @click="edit(scope.row)"></el-button>-->
            <el-popconfirm title="" v-if="scope.row.status === 'PENDING'"
                           @confirm="passAppointment(scope.row)">
              <el-button slot="reference" type="primary" style="margin-left: 5px"></el-button>
            </el-popconfirm>
            <el-popconfirm title="" v-if="scope.row.status === 'PENDING'"
                           @confirm="cancelAppointment(scope.row)">
              <el-button slot="reference" type="warning" style="margin-left: 5px"></el-button>
            </el-popconfirm>
            <el-popconfirm title="" v-if="scope.row.status === 'APPROVED'"
                           @confirm="finishAppointment(scope.row)">
              <el-button slot="reference" type="success" style="margin-left: 5px"></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="margin-top: 10px">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[20,50,100]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "UserAppointment",
  data() {
    return {
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      params: {
        salonId: 1,
        name: '',
        phone: '',
        pageNum: 1,
        pageSize: 20
      },
      roles: [{role: "TECHNICIAN", roleName: ""}, {role: "MANAGER", roleName: ""}],
      tableData: [],
      salonList: [],
      total: 0,
      dialogFormVisible: false,
      form: {},
      flag: false
    }
  },
  // created
  created() {
    this.loadSalonList();
    this.findBySearch();
  },
  // 
  methods: {
    loadSalonList() {
      request.get("/salon/findAll", {
        params: null
      }).then(res => {
        console.log(res);
        if (res.code === 0) {
          console.log("");
          let salonList = res.data;
          this.salonList = salonList;
          this.params.salonId = salonList[0].id;
        } else {
          // this.$message.error(res.msg);
          this.$notify({
            title: '',
            message: '(/salon/findAll)' + res.msg + '',
            type: 'warning'
          });
          // this.$router.push("/login");
        }
      }).catch(err => {
        console.log(err);
      });
    },
    findBySearch() {
      request.get("/salon/findBySalonId?salonId=" + this.params.salonId, {
        params: null
      }).then(res => {
        if (res.code === 0) {
          this.tableData = res.data;
          this.total = res.data.length;
        } else {
          this.$message.error(res.msg);
          // this.$router.push("/login");
        }
      }).catch(err => {
        console.log(err);
      });
    },
    reload() {
      this.flag = false;
      this.dialogFormVisible = false
      this.findBySearch();
    },
    handleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.findBySearch();
    },
    handleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.findBySearch();
    },
    passAppointment(appointment) {
      let data = {
        id: appointment.id,
        status: 'APPROVED'
      };

      request.post("/salon/setAppointmentStatus", JSON.stringify(data))
          .then(res => {
            if (res.code === 0) {
              this.$message({
                message: '',
                type: 'success'
              });
              this.findBySearch();
            } else {
              this.$message({
                message: '' + res.msg,
                type: 'warning'
              });
              this.findBySearch();
            }
          }).catch(err => {
        console.log(err);
      });
    },
    cancelAppointment(appointment) {
      let data = {
        id: appointment.id,
        status: 'CANCELED'
      };

      request.post("/salon/setAppointmentStatus", JSON.stringify(data)).then(res => {
        if (res.code === 0) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.findBySearch();
        } else {
          this.$message({
            message: '' + res.msg,
            type: 'warning'
          });
          this.findBySearch();
        }
      }).catch(err => {
        console.log(err);
      });
    },
    finishAppointment(appointment) {
      let data = {
        id: appointment.id,
        status: 'COMPLETED'
      };

      request.post("/salon/setAppointmentStatus", JSON.stringify(data)).then(res => {
        if (res.code === 0) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.findBySearch();
        } else {
          this.$message({
            message: '' + res.msg,
            type: 'warning'
          });
          this.findBySearch();
        }
      }).catch(err => {
        console.log(err);
      });
    }
  }
}
</script>
